import common from '../../common/common.js'
import util from '../../common/util.js'
import uniPopup from '../../components/uni-popup.vue'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			userid:'',
			userInfo:[],
			ads:[],
			msg : [],
			rule:[],
			popType: 'middle',
			showPopupMiddle: false,				
			qrval:'',
			qrsize:170,
			qrcode :'',
			showHaibaoMiddle:false,
			haibao:'',
			userList:[],
		}
	},
	onLoad() {
		var that = this
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');
		
		that.userid = userid
		that.userInfo = userInfo
		
		that.qrval = '/pages/index/index?tjuid='+userid
		
		that.getInviteDetailAds();
		that.getInviteRule();
		that.getInviteLogs();
		that.getInviteUserList();
		
		
	},
	onPullDownRefresh:function(){
	  var that = this
	  uni.showNavigationBarLoading();
	  console.log('下拉刷新中...');	
	  
	  that.getInviteDetailAds();
	  that.getInviteRule();
	  that.getInviteLogs();
	  that.getInviteUserList();
	  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	onShareAppMessage(res) {
		var that = this
		var userid = that.userid
		if (res.from === 'button') {// 来自页面内分享按钮
		  console.log(res.target)
		}
		return {
		  title: '商家活动广告平台——众晨星',
		  path: '/pages/index/index?tjuid='+userid
		}
	},
	methods: {
		
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupMiddle = false;
		},
		//展示居中 popup
		showMiddlePopup: function() {
			this.hidePopup();
			this.showPopupMiddle = true;
			this.creatQrcode();
		},			
		hideHaibao(){
			var that = this
			that.showHaibaoMiddle = false;
		},
		//海报
		popHaibao(){
			var that = this
			that.hideHaibao();
			that.showHaibaoMiddle = true;
			that.creatHaibao();
		},
		//保存海报
		saveHaibao(){
			var that = this
			var imgSrc = that.haibao
			console.log(imgSrc)
			uni.downloadFile({
				url: imgSrc, //仅为示例，并非真实的资源
				success: (res) => {
					//console.log(res);
					uni.saveImageToPhotosAlbum({
					  filePath: res.tempFilePath,
					  success(result) {
						uni.showModal({
							title:'提示',
							content:'保存相册成功',
							success() {
								that.hideHaibao();
							}
						})
					  }
					})
				}
			});
		},
		//获取海报
		creatHaibao(){
			var that = this
			var userid = uni.getStorageSync('userid');
			uni.request({
				url: common.baseUrl + 'sharepic/getInviteHaibao',
				method: 'GET',
				data: {
					uid:userid
				},
				success: res => {
					//console.log("qrcode",res);
					that.haibao = res.data.result
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//邀请列表
		getInviteLogs(){
			var that = this
			uni.request({
				url: common.apiUrl + 'user/getInviteLogs',
				method: 'GET',
				data: {},
				success: res => {
					console.log("logs",res);
					that.msg = res.data.logs;
				},
			});
		},
		//邀请规则
		getInviteRule(){
			var that = this
			uni.request({
				url: common.apiUrl + 'ads/getInviteRule',
				method: 'GET',
				data: {},
				success: res => {
					that.rule = res.data.rule;
				},
			});
		},
		//邀请详情广告
		getInviteDetailAds(){
			var that = this
			uni.request({
				url: common.apiUrl + 'ads/getInviteDetailAds',
				method: 'GET',
				data: {},
				success: res => {
					that.ads = res.data.ads
				},
				fail: () => {},
				complete: () => {}
			});
		},
		creatQrcode(){
			//this.$refs.qrcode.creatQrcode();
			var that = this
			var userid = uni.getStorageSync('userid');
			uni.request({
				url: common.baseUrl + 'sharepic/getInviteQrcode',
				method: 'GET',
				data: {
					uid:userid
				},
				success: res => {
					//console.log("qrcode",res);
					that.qrcode = res.data.qrcode
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取邀请用户列表
		getInviteUserList(){
			var that = this
			var userid = uni.getStorageSync('userid');
			uni.request({
				url: common.apiUrl + 'user/getInviteUserList',
				method: 'GET',
				data: {
					uid:userid
				},
				success: res => {
					console.log(res);
					that.userList = res.data.user_list
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	components: {
		uniPopup,
	}
}
